html, body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
  background-color: #fff;
  color: #222;
}

section {
  padding: 80px 0;
}

h1, h2, h3, h4 {
  font-weight: 600;
  color: #222;
}

a {
  text-decoration: none;
  color: inherit;
}

.btn {
  display: inline-block;
  background: #111;
  color: #fff;
  border: none;
  padding: 12px 28px;
  border-radius: 4px;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.btn:hover {
  background: #333;
}

.section-title {
  text-align: center;
  margin-bottom: 50px;
}

.section-title h3 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.section-title span {
  display: block;
  width: 60px;
  height: 3px;
  background: #111;
  margin: 0 auto;
}
@media (max-width: 991px) {
  .header-area .main-nav .menu-trigger {
    display: block;
  }
  .menu-trigger {
    border: none;
    background-color: transparent;
  }

  .header-area .main-nav .nav {
    display: none;
    position: absolute;
    width: 100%;
    top: 80px;
    left: 0;
    background: #fff;
    text-align: center;
    border-radius: 15px 15px 15px 15px;
    
    flex-direction: column;
    padding: 20px 0;
  }

  .header-area .main-nav .nav.active {
    display: flex;
  }

  .header-area .main-nav .nav li {
    padding: 12px 0;
    border: none;
  }

  .header-area .main-nav .nav li a {
    color: #000; /* white text for contrast */
  }

  .header-area .main-nav .nav li a:hover {
    color: #a3c9ff; /* soft blue hover effect */
  }
}

